<template>
  <va-card :title="$t('dashboard.table.title')">
    <div class="row align--center mb-1">
      <div class="flex xs12 sm6">
          <va-input
          class="ma-0"
          :value="term"
          :placeholder="$t('tables.searchByName')"
          @input="search"
          removable
        >
          <va-icon name="fa fa-search" slot="prepend" />
        </va-input>
      </div>

        
       <!-- <stateselect :stringProp="stringMessage" v-on:childToParent="onChildClick"></stateselect> -->
      
        <!-- <div class="d-flex justify--end">
          <va-button-toggle
            outline
            v-model="mode"
            :options="modeOptions"
            style="max-width: 100%;"
          />
        </div> -->
      
    </div>

    <va-data-table
      :fields="mode ? detailedFields : fields"
      :data="filteredData"
      :loading="loading"
      hoverable
    >
      <template slot="icon">
        <va-icon name="fa fa-exclamation-circle" />
      </template>
      <template v-slot:starred="props">
        <va-icon
          v-if="props.rowData.starred"
          name="fa fa-star"
          color="warning"
        />
      </template>
      <template v-slot:status="props">
        <va-badge :color="getStatusColor(props.rowData.zone)">
          {{ props.rowData.zone }}
        </va-badge>
      </template>

    </va-data-table>
  </va-card>
</template>

<script>
import debounce from 'lodash/debounce'
import axios from 'axios'
// import data from '../markup-tables/data.json'
// import Stateselect from '../ui/popovers/Stateselect';

export default {
  
  data () {
    return {
      data:[],
      data2: [],
      fromChild: '',
      stateIndex: 0,
      users:'',
      loading: false,
      term: null,
      mode: 0,
    }
  },
  computed: {
    fields () {
      return [{
        name: '__slot:icon',
        width: '30px',
        dataClass: 'text-center',
      }, {
        name: 'district',
        title: this.$t('tables.headings.name'),
        width: '30%',
        dataClass: 'text-center',
        titleClass: 'text-center',
      }, 
      {
         name: 'state',
         title: this.$t('tables.headings.email'),
         width: '30%',
         dataClass: 'text-center',
         titleClass: 'text-center',
      }, 
      {
        name: '__slot:status',
        title: this.$t('tables.headings.status'),
        width: '40%',
        dataClass: 'text-center',
        titleClass: 'text-center',
        // sortField: 'status',
      },
      {
        name: 'lastupdated',
        title: this.$t('tables.headings.lastupdate'),
        width: '30%',
        titleClass: 'text-center',
        titlesize: '20px',
  
      },
       {
        name: '__slot:actions',
        dataClass: 'text-right',
      }]
    },
    detailedFields () {
      return [{
        name: '__slot:icon',
        width: '30px',
        dataClass: 'text-center',
      }, {
        name: 'district',
        title: this.$t('tables.headings.name'),
        width: '20%',
      }, 
      // {
      //   name: 'email',
      //   title: this.$t('tables.headings.email'),
      //   width: '20%',
      // },
      {
        name: 'state',
        title: this.$t('tables.headings.location'),
        with: '20%',
      },
      {
        name: '__slot:status',
        title: this.$t('tables.headings.status'),
        width: '40%',
        sortField: 'status',
      },
      
      // ,{
      //   name: '__slot:actions',
      //   dataClass: 'text-right',
      // }
      ]
    },
    modeOptions () {
      return [{
        value: 0,
        label: this.$t('dashboard.table.brief'),
      }, {
        value: 1,
        label: this.$t('dashboard.table.detailed'),
      }]
    },
    filteredData () {
      if (!this.term || this.term.length < 1) {
        return this.users
      }
      // if (!this.term || this.term.length < 1  this.fromChild.length>1) {
      //   return this.users.filter(item => {
      //   return item.state==this.fromChild
     
      // } )}
      return this.users.filter(item => {
        return item.state.toLowerCase().startsWith(this.term.toLowerCase()) || item.district.toLowerCase().startsWith(this.term.toLowerCase())
      })
    },
  },
  mounted(){

    axios
      .get("https://api.covid19india.org/zones.json")
      .then(response => {
        const products = response.data;
        this.data= products['zones'];
        // console.log(this.data[0])
        
      this.users= this.data.slice()
      });
      },
  methods: {
    onChildClick (value1, value2) {
      this.fromChild = value1;
      this.stateIndex = value2;
      // console.log(this.fromChild, this.stateIndex);
    },
    getStatusColor (zone) {
      if (zone === 'Green') {
        return 'success'
      }

      if (zone === 'Orange') {
        return 'warning'
      }

      return 'danger'
    },
    resolveUser (user) {
      this.loading = true

      setTimeout(() => {
        const idx = this.users.findIndex(u => u.id === user.id)
        this.users.splice(idx, 1)
        this.loading = false

        this.showToast(this.$t('dashboard.table.resolved'), {
          icon: 'fa-check',
          position: 'bottom-right',
          duration: 1500,
        })
      }, 500)
    },
    search: debounce(function (term) {
      this.term = term
    }, 400),
  },
}
</script>

<style lang="scss">

</style>
