<template>
  <div>
  

    <div class="flex">
      <va-card :title="$t('dashboard.charts.trendyTrends')">
        <!-- <va-button
          small
          slot="actions"
          color="success"
          class="mr-0"
          @click="deleteSection"
          :disabled="lineChartData.labels.length < 2"
        >
          {{ $t('dashboard.charts.showInMoreDetail') }}
        </va-button> -->
        <va-chart class="chart" ref="lineChart" :data="lineChartData" type="line"/>
      </va-card>
      </div>
      <br>
      <div class="row row-equal">
        <div class="flex xs12 xl6">
      <va-card :title="$t('charts.verticalBarChart')"
        >
          <va-chart class="chart" :data="verticalBarChartData" type="vertical-bar"/>
        </va-card>
  
    </div>
         <div class="flex xs12 xl6">
      <va-card title="Latest News">
        
         <!-- <div class="flex xs6 md6 x32"> -->
      <va-card>
        <a :href= "newslink[0]" style="color: #696969;"><i class="fa fa-external-link" aria-hidden="true"></i>   {{newstitle[0]}}</a>
      </va-card>
      <br>
      <va-card>
        <a :href= "newslink[1]" style="color: #696969;"><i class="fa fa-external-link" aria-hidden="true"></i>   {{newstitle[1]}}</a>
        </va-card>
        <br>
        <va-card>
        <a :href= "newslink[2]" style="color: #696969;"><i class="fa fa-external-link" aria-hidden="true"></i>   {{newstitle[2]}}</a>
      </va-card>
      <br>
      <va-card>
        <a :href= "newslink[3]" style="color: #696969;"><i class="fa fa-external-link" aria-hidden="true"></i>   {{newstitle[3]}}</a>
      </va-card>
        <div class="text--center">
            <va-button outline color="dark" @click="$router.push('news')">Read More News</va-button>
            </div>
    <!-- </div> -->
        
        
        </va-card>
  
    </div>


    
      </div>
      <!-- <va-card :title="$t('dashboard.charts.loadingSpeed')">
        <va-button
          icon="fa fa-print"
          flat
          slot="actions"
          class="mr-0"
          @click="printChart"
        />
        <va-chart class="chart chart--donut" :data="donutChartData" type="donut"/>
      </va-card>
    </div>

    <div class="flex xs12 md6 xl3">
      <dashboard-contributors-chart/>
    </div> -->
  </div>
</template>

<script>
import { getDonutChartData } from '../../data/charts/DonutChartData'
// import { getLineChartData } from '../../data/charts/LineChartData'
import axios from 'axios'
import { getVerticalBarChartData } from '../../data/charts/VerticalBarChartData'
import DashboardContributorsChart from './DashboardContributorsList'
import { hex2rgb } from '../../services/vuestic-ui'

export default {
  name: 'dashboard-charts',
  components: { DashboardContributorsChart },
  data () {
    return {
      // verticalBarChartData: getVerticalBarChartData(this.$themes),
      parsedData:'',
      parsedalldates:'',
      dates:[],
      newstitle:[],
      newslink:[],
      verticalBarChartData:{},
      lineChartData: {},
      donutChartData: getDonutChartData(this.$themes),
      lineChartFirstMonthIndex: 0,
    }
  },
  mounted() {
    axios
      .get("https://api.covid19india.org/data.json")
      .then(response => {
        const products = response.data;
        this.parsedData=products.cases_time_series
        // alert(this.stringstate)
        this.dates=this.parsedData.slice(this.parsedData.length-14, this.parsedData.length)
    
    this.verticalBarChartData={
    'labels':  this.dates.map(a => a.date),
    'datasets': [
    {
      label: 'Confirmed',
      backgroundColor:this.$themes.info,
      borderColor: 'transparent',
      data: this.dates.map(a => a.dailyconfirmed).map(Number),
    },
    {
      label: 'Recovered',
      backgroundColor: this.$themes.success,
      // borderColor: 'transparent',
      data: this.dates.map(a => a.dailyrecovered).map(Number),
    },
    {
      label: 'Deceased',
      backgroundColor:(this.$themes.default,1.6),
      borderColor: 'transparent',
      data: this.dates.map(a => a.dailydeceased).map(Number),
    },
  ],
}
    });
    // console.log(this.verticalBarChartData)
//  console.log(this.lineChartData) 
axios
      .get("https://api.covid19india.org/data.json")
      .then(response => {
        const alldate = response.data;
        this.parsedalldates=alldate.cases_time_series
        // alert(this.stringstate)
        this.dates=this.parsedData.slice(this.parsedData.length-14, this.parsedData.length)
    

 this.lineChartData= {
      labels: this.parsedalldates.slice(54, this.parsedData.length).map(a => a.date),
      // labels: ['one,','two','three','four','5','6'],
      datasets: [
        {
          label: 'Daily Confirmed',
          backgroundColor: hex2rgb(this.$themes.info, 0.6).css,
          borderColor: 'transparent',
          data:this.parsedalldates.slice(54, this.parsedData.length).map(a => a.dailyconfirmed).map(Number),
        },
        {
          label:'Daily Recovered',
          backgroundColor: hex2rgb(this.$themes.success, 1).css,
          borderColor: 'transparent',
          data:this.parsedalldates.slice(54, this.parsedData.length).map(a => a.dailyrecovered).map(Number),
        },
        {
          label:'Daily Deceased',
          backgroundColor: hex2rgb(this.$themes.default, 1.6).css,
          borderColor: 'transparent',
          data:this.parsedalldates.slice(54, this.parsedData.length).map(a => a.dailydeceased).map(Number),
        },
      ],
    }

//  console.log(this.lineChartData) 
});


 axios
      .get("https://newsapi.org/v2/top-headlines?country=in&apiKey=YOUR-KEY-HERE")
      .then(response => {
        const allnews = response.data;
        // this.infoTiles[0].value = products.data.articles;
        const ParsedData=allnews["articles"]
        // console.log(response.data['articles'])
        this.newstitle= ParsedData.map(a => a.title)
        this.newslink= ParsedData.map(a => a.url)
        // console.log(this.newsimgtitle)
        // alert("all done")
        });

  },
  watch: {
    '$themes.primary' () {
      this.lineChartData = getLineChartData(this.$themes)
      // console.log(this.lineChartData)
      this.donutChartData = getDonutChartData(this.$themes)
    },

    '$themes.info' () {
      this.lineChartData = getLineChartData(this.$themes)
      this.donutChartData = getDonutChartData(this.$themes)
    },

    '$themes.danger' () {
      this.donutChartData = getDonutChartData(this.$themes)
    },
  },
  methods: {
    deleteSection () {
      this.lineChartFirstMonthIndex += 1
      this.lineChartData = getLineChartData(this.$themes, this.lineChartFirstMonthIndex)
      this.$refs.lineChart.$refs.chart.refresh()
    },
    printChart () {
      const win = window.open('', 'Print', 'height=600,width=800')
      win.document.write(`<br><img src='${this.donutChartDataURL}'/>`)
      // TODO: find better solution how to remove timeout
      setTimeout(() => {
        win.document.close()
        win.focus()
        win.print()
        win.close()
      }, 200)
    },
  },
  computed: {
    donutChartDataURL () {
      return document.querySelector('.chart--donut canvas').toDataURL('image/png')
    },
  },
}
</script>

<style scoped>
  .chart {
    height: 400px;
  }

  </style>
