<template>

  <div class="row row-equal">
  
    <div class="flex xl6 xs12">
      <div class="row">
        <div class="flex xs12 sm4" v-for="(info, idx) in infoTiles" :key="idx">
          <va-card class="mb-4" :color="info.color">
            <p class="display-2 mb-0" style="color: white;">{{ info.value }}</p>
            <p>{{ $t("dashboard.info." + info.text) }}</p>
          </va-card>
        </div>
      </div>

      <div class="row">
        <div class="flex xs12 md6">
          <va-card>
            <!-- <p class="display-2 mb-1" :style="{color: this.$themes.primary}">291</p> -->
            <p class="no-wrap" :style="{ color: this.$themes.primary }">
              {{ $t("popovers.selectstate") }}
            </p>
            <stateselect :stringProp="stringMessage" v-on:childToParent="onChildClick"></stateselect>
          </va-card>
        </div>
        <div class="flex xs12 md6">
          <va-card>
            <div class="row row-separated">
              <div class="flex xs12">
                <p
                  class="display-2 mb-1 text--center"
                  :style="{ color: this.$themes.info }"
                >
                  <span style="font-size: 24px; color: #a9a9a9;">Infected </span>{{ stateinfected }}</p>
                  <p
                  class="display-2 mb-1 text--center"
                  :style="{ color: this.$themes.success }"
                >
                 <span style="font-size: 24px; color: #a9a9a9;">Cured </span> {{ statecured }}</p>
                  <p
                  class="display-2 mb-1 text--center"
                  :style="{ color: this.$themes.dark }"
                >
                  <span style="font-size: 24px; color: #a9a9a9;">Deceased </span>{{ statedeath }}</p>
                <!-- <p class="text--center mb-1">
                  {{ $t("dashboard.info.infected") }}
                </p> -->
              </div>
              <!-- <div class="flex xs12"> -->
                <!-- <p
                  class="display-2 mb-1 text--center"
                  :style="{ color: this.$themes.info }"
                >
                  {{ statecured }}
                </p> -->
                <!-- <p class="text--center no-wrap mb-1">
                  {{ $t("dashboard.info.recovered") }}
                </p> -->
              <!-- </div> -->
              <!-- <div class="flex xs12">
                <p
                  class="display-2 mb-1 text--center"
                  :style="{ color: this.$themes.warning }"
                >
                  {{ statedeath }}
                </p> -->
                <!-- <p class="text--center mb-1">
                  {{ $t("dashboard.info.deaths") }}
                </p> -->
              <!-- </div> -->
            </div>
          </va-card>
        </div>
      </div>
    </div>

    <div class="flex xs12 md6 xl3">
      <va-card :title="$t('dashboard.info.componentRichTheme')">
        <pie :stringstate= "fromChild"></pie>
      </va-card>
    </div>
    <div class="flex xs12 md6 xl3">
      <va-card>
          <div class="text--bold text--center"> 
            Its time that we join our hands and help those in need. Even the smallest contribution from your end can make a huge difference in someone's life right now.
            <br>Do your bit.
          </div>
          <div class="text--center">
            <va-button outline color="dark" @click="$router.push('donate')">Be a Hero</va-button>
            </div>
      </va-card>
    </div>
      <!-- <div class="row">
      <div class="flex xs12">
        <va-card :title="$t('charts.verticalBarChart')"
        >
          <va-chart class="chart" :data="verticalBarChartData" type="vertical-bar"/>
        </va-card>
       </div>
      </div> -->
     
    <va-modal v-model="modal">
      <div style="position: relative;">
        <va-button
          @click="showPrevImage"
          icon="fa fa-chevron-left"
          flat
          style="position: absolute; top: 50%;"
        />
        <va-button
          @click="showNextImage"
          icon="fa fa-chevron-right"
          flat
          style="position: absolute; top: 50%; right: 0;"
        />
        <transition>
          <img
            :src="images[currImage]"
            style="height: 50vh; max-width: 100%;"
          />
        </transition>
      </div>
    </va-modal>
  </div>
</template>

<script>
import axios from 'axios';
import Stateselect from '../ui/popovers/Stateselect';

import { getVerticalBarChartData } from '../../data/charts/VerticalBarChartData'
import DashboardTable from './DashboardTable'
import Pie from '../statistics/charts/Pie'
export default {
  components: {
    Stateselect,
    Pie,
    DashboardTable
  },
  name: 'DashboardInfoBlock',
  data() {
    return {
      stringMessage: 'childToParent',
      fromChild: '',
      dates:[],
      parsedData:{},
      stateIndex:0,
      infoTiles: [
        {
          color: 'info',
          value: [],
          text: 'infected',
          icon: ''
        },
        {
          color: 'success',
          value: [],
          text: 'recovered',
          icon: ''
        },
        {
          color: 'dark',
          value: [],
          text: 'deaths',
          icon: ''
        }
      ],
      stateinfected: 0,
      statecured: 0,
      statedeath: 0,
      modal: false,
      currImage: 0,
      images: [
        "https://i.imgur.com/qSykGko.jpg",
        "https://i.imgur.com/jYwT08D.png",
        "https://i.imgur.com/9930myH.jpg",
        "https://i.imgur.com/2JxhWD6.jpg",
        "https://i.imgur.com/MpiOWbM.jpg"
      ]
    };
  },

  mounted() {
    axios
      .get("https://api.rootnet.in/covid19-in/stats/latest")
      .then(response => {
        const products = response.data;
        this.infoTiles[0].value = products.data.summary.total;

        this.infoTiles[1].value = products.data.summary.discharged;
        this.infoTiles[2].value = products.data.summary.deaths;
      });

    axios
      .get("https://api.covid19india.org/data.json")
      .then(response => {
        const products = response.data;
        this.parsedData=products['statewise'].filter(({ state }) => state === this.fromChild);
        // alert(this.stringstate)
        // console.log(this.parsedData[0])
        
        // console.log(this.parsedData.filter(({ active }) => active))
        // console.log(this.parsedData[0].active)
        // console.log(this.parsedData.active)
        this.stateinfected=this.parsedData[0].confirmed;
        this.statedeath= this.parsedData[0].deaths;
        this.statecured= this.parsedData[0].recovered;
      });

    axios
      .get("https://api.covid19india.org/data.json")
      .then(response => {
        const products = response.data;
        this.parsedData=products.cases_time_series
        // alert(this.stringstate)
        this.dates=this.parsedData.slice(this.parsedData.length-14, this.parsedData.length)
    
    this.verticalBarChartData={
    'labels':  this.dates.map(a => a.date),
    'datasets': [
    {
      label: 'Confirmed',
      backgroundColor:this.$themes.primary,
      borderColor: 'transparent',
      data: this.dates.map(a => a.dailyconfirmed).map(Number),
    },
    {
      label: 'Deceased',
      backgroundColor:this.$themes.info,
      borderColor: 'transparent',
      data: this.dates.map(a => a.dailydeceased).map(Number),
    },{
      label: 'Recovered',
      backgroundColor: this.$themes.warning,
      // borderColor: 'transparent',
      data: this.dates.map(a => a.dailyrecovered).map(Number),
    },
  ],
}
    });


  },
   watch: {
    'stateIndex': function (newValue, oldValue) {
      axios
      .get("https://api.covid19india.org/data.json")
      .then(response => {

        const products = response.data;
        // console.log(products['statewise'])
        // var raw=products['raw_data']
        this.parsedData=products['statewise'].filter(({ state }) => state === this.fromChild);
        // alert(this.stringstate)
        // console.log(this.parsedData[0])
        this.stateinfected=this.parsedData[0].confirmed;
        this.statedeath = this.parsedData[0].deaths;
        this.statecured = this.parsedData[0].recovered;

        // const products = response.data;
        // this.stateinfected = products.data.regional[this.stateIndex].totalConfirmed;

        // this.statecured = products.data.regional[this.stateIndex].discharged;
        // this.statedeath = products.data.regional[this.stateIndex].deaths;
      });

      // here you can do whatever you want
    // this.$emit('childToParent',this.popover.color,this.colors.indexOf(this.popover.color))
    }
  },
  
  methods: {
     onChildClick (value1, value2) {
      this.fromChild = value1;
      this.stateIndex = value2;
      // console.log(this.fromChild, this.stateIndex);
    },
    showModal() {
      this.modal = true;
    },
    showPrevImage() {
      this.currImage = !this.currImage
        ? this.images.length - 1
        : this.currImage - 1;
    },
    showNextImage() {
      this.currImage =
        this.currImage === this.images.length - 1 ? 0 : this.currImage + 1;
    }
  }
};
</script>

<style lang="scss">
.row-separated {
  .flex + .flex {
    border-left: 1px solid #e3eaeb;
  }

  @include media-breakpoint-down(xs) {
    p:not(.display-2) {
      font-size: 0.875rem;
    }
  }
}

.dashboard {
  .va-card__header--over {
    @include media-breakpoint-up(md) {
      padding-top: 0 !important;
    }
  }

  .goleft {
    max-height: 30px !important;
  }

  .va-card__image {
    @include media-breakpoint-up(md) {
      padding-bottom: 0 !important;
    }
  }
}
</style>
