<template>
  <div class="flex xs12 md12">
      <span>
    <va-select  class="mb-6" v-model="popover.color" :options="colors" />
    <!-- <p v-on:mouseover="emitToParent" >{{ stringProp }}</p> -->
  </span>
  </div>
</template>

<script>
export default {

      data () { userInput:''
            return {
                    colors: [
                    'Andaman and Nicobar Islands',
                    'Andhra Pradesh',
                    'Arunachal Pradesh',
                    'Assam',
                    'Bihar',
                    'Chandigarh',
                    'Chhattisgarh',
                    'Delhi',
                    'Goa',
                    'Gujarat',
                    'Haryana',
                    'Himachal Pradesh',
                    'Jammu and Kashmir',
                    'Jharkhand',
                    'Karnataka',
                    'Kerala',
                    'Ladakh',
                    'Madhya Pradesh',
                    'Maharashtra',
                    'Manipur',
                    'Meghalaya',
                    'Mizoram',
                    'Odisha',
                    'Puducherry',
                    'Punjab',
                    'Rajasthan',
                    'Tamil Nadu',
                    'Telengana',
                    'Tripura',
                    'Uttarakhand',
                    'Uttar Pradesh',
                    'West Bengal'
                ],
                popover: {
                    color: "Delhi"
                    }
        }
  },
    props: {
    stringProp: String
  },
  components: {},
    mounted: function (){
        // console.log(this.popover.color)
        this.$emit('childToParent',this.popover.color,this.colors.indexOf(this.popover.color))
        
    },
    watch: {
    'popover.color': function (newValue, oldValue) {
      // here you can do whatever you want
      this.$emit('childToParent',this.popover.color,this.colors.indexOf(this.popover.color))
    }
  },
}
</script>

<style lang="scss">
.tooltips {
  &__content {
    @include va-flex-center();
  }
}
</style>
