<template>
  <div class="charts"> 

      <div 
          class="chart-widget mycls"
          :title="$t('charts.piechart')">
        
          <va-chart  class="pieclass" :data="pieChartData" type="donut"/>
         </div>
<!-- 
          <div class="flex md6 xs12">
        <va-card
          class="chart-widget"
          :title="$t('charts.donutChart')"
        >
          <va-chart :data="donutChartData" type="donut"/>
        </va-card>
      </div> -->
    
    
  </div>
</template>

<script>
import { getPieChartData } from '../../../data/charts/PieChartData'
import axios from 'axios'
export default {
  name: 'pie',
  props: {
    stringstate: String
  },
  data () {
    return {
    pieChartData:'',
    // ,this.noage.length,this.smallage.length,this.midage.length,this.largeage.length),
    parsedData:'',
    flag:0,
    act:0,
    death:20,
    recov:0
      }
  },

  
  mounted() {
    axios
      .get("https://api.covid19india.org/data.json")
      .then(response => {
        const products = response.data;
        // console.log(products['statewise'])
        // var raw=products['raw_data']
        this.parsedData=products['statewise'].filter(({ state }) => state === this.stringstate);
        // alert(this.stringstate)
        // console.log(this.parsedData[0])
        
        // console.log(this.parsedData.filter(({ active }) => active))
        // console.log(this.parsedData[0].active)
        // console.log(this.parsedData.active)
        this.act=this.parsedData[0].active;
        this.death = this.parsedData[0].deaths;
        this.recov = this.parsedData[0].recovered;
        // this.largeage = this.parsedData.filter(d => d.agebracket != '' && d.agebracket > 55);
            // this.infoTiles[0].value = products.data.summary.total;
        // console.log(this.act);
        // console.log(this.death);
        // console.log(this.recov);
        // console.log(this.largeage);
        // this.infoTiles[1].value = products.data.summary.discharged;
        // this.infoTiles[2].value = products.data.summary.deaths;
        
       
        this.pieChartData= getPieChartData(this.$themes,this.act,this.death,this.recovered);
        });
  },

  watch: {
    'stringstate': function (newValue, oldValue) {
      axios
      .get("https://api.covid19india.org/data.json")
      .then(response => {
        const products = response.data;
        // console.log(products['statewise'])
        // var raw=products['raw_data']
        this.parsedData=products['statewise'].filter(({ state }) => state === this.stringstate);
        // alert(this.parsedData)
        
        this.act=this.parsedData[0].active;
        this.death = this.parsedData[0].deaths;
        this.recov = this.parsedData[0].recovered;// this.largeage = this.parsedData.filter(d => d.agebracket != '' && d.agebracket > 55);
            // this.infoTiles[0].value = products.data.summary.total;
        // console.log(this.act);
        // console.log(this.death);
        // console.log(this.recov);// this.infoTiles[1].value = products.data.summary.discharged;
        // this.infoTiles[2].value = products.data.summary.deaths;
        
        this.pieChartData= getPieChartData(this.$themes,this.act,this.death,this.recov);
      });
      // here you can do whatever you want
    // this.$emit('childToParent',this.popover.color,this.colors.indexOf(this.popover.color))
    }
  },

  methods: {
    refreshData () {
      this.lineChartData = getLineChartData(this.$themes)
    },
  },
}
</script>

